.nav {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.2rem;
  /* height: 60px; */
  padding: 0.5rem;
  box-shadow: 0px 25px 30px -15px #171C23 ;
  color: #88afaf;
}

.nav > .nav-header {
  display: inline;
}

.nav > .nav-header > .nav-title {
  display: inline-block;
  font-size: 22px;
  color: #fff;
  padding: 10px 10px 10px 10px;
  font-weight: bolder;
}

.nav > .nav-btn {
  display: none;
}

.nav > .nav-links {
  display: inline;
  font-size: 18px;
  font-weight: 300;
  text-transform: uppercase;
}

.nav > .nav-links > a:hover,
.nav > .nav-links > a:active {
  color:#d79d94;
}

.nav > .nav-links > a {
  position: relative;
  display: inline-block;
  padding: 13px 10px 13px 10px;
  text-decoration: none;
  color: #88afaf;
  font-weight: bold;
}
.zainab{
  color: #5a4951;
  font-weight: bold;
}

.nav > .nav-links > a,
.nav > .nav-links > a:after,
.nav > .nav-links > a:before {
  transition: all 0.5s;
}
.nav > .nav-links > a::after {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 0%;
  content: ".";
  color: transparent;
  background:#ffffff;
  height: 1px;
}
.nav > .nav-links > a:hover:after {
  width: 100%;
}
.nav > #nav-check {
  display: none;
}

@media (max-width: 600px) {
  .nav > .nav-btn {
    display: inline-block;
    position: absolute;
    right: 0px;
    top: 0px;
    background-color:#88afaf;
  }
  .nav > .nav-btn > label {
    display: inline-block;
    width: 50px;
    height: 50px;
    padding: 13px;
  }
  .nav > .nav-btn > label:hover,
  .nav #nav-check:checked ~ .nav-btn > label {
    background-color: #eceaea;
    transition: all 0.5s;
  }
  .nav > .nav-btn > label > span {
    display: block;
    width: 25px;
    height: 10px;
    border-top: 2px solid #d79d94;
  }
  .nav > .nav-links {
    position: absolute;
    display: inline-block;
    width: 100%;
    background-color:  #e6b6ae;
    height: 0px;
    transition: all 0.3s ease-in;
    overflow-y: hidden;
    top: 50px;
    left: 0px;
    text-align: center;
    z-index: 1111;
  }
  .nav > .nav-links > a {
    display: block;
    width: 100%;
  }
  .nav > #nav-check:not(:checked) ~ .nav-links {
    height: 0px;
  }
  .nav > #nav-check:checked ~ .nav-links {
    height: calc(100vh - 50px);
    overflow-y: auto;
  }
  .btn-home {
    display: none;
  }
}


